@import '../../assets/sass/main.scss';

.border-country {
  @include flex-center-all;
  background: $dark-blue;
  padding: 0.3em 1.5em;
  border-radius: 4px;
  box-shadow: $shadow;
  transition: $smooth;

  &__name {
    font-size: 0.9em;
  }
  &:hover {
    box-shadow: 1px 2px 12px 0px #999898;
  }
}
